import getField from './fieldMapping'
import StaticField from './StaticField'

export default {
  name: 'GenericInput',
  functional: true,
  props: {
    name: {},
    type: {
      type: String, // editor
      default: 'str'
    },
    value: {},  // 
    static: {
      type: Boolean,
      default: false
    }, //
    enableOnChange: { // onChangeBuildinput
      type: Boolean,
      default: true
    },
    onChange: {},
    options: {}, // 
    on: {}, // 
    multiple: {
      type: Boolean,
      default: false
    },
    format: {}, //
    placeholder: {},
    info: {},
    required: {
      type: Boolean,
      default: false
    },
    staticSuffix: {
      type: String,
      default: '_static'
    },
    root: String, // change,
    staticComponent: {
      default () {
        return StaticField
      }
    },
    showTitle: {
      default: false
    },
    classes: {},
    labelField: { // select
      type: String,
      deafult: ''
    }
  },

  render (h, ctx) {
    let self = ctx.props
    let static_name = `${self.name}${self.staticSuffix}`
    let InputClass = getField(self.type)
    let input = new InputClass(ctx.parent, ctx.props)
    if (self.value[self.name] !== undefined) {
      if (!self.labelField)
        input.setStaticValue(self.value[self.name])
      else
        input.setStaticValue(self.value[self.labelField], true)
    }
    // 
    if (self.static && self.staticComponent) {
      return h(self.staticComponent, {props: ctx.props})
    }

    return input.render(h, ctx)
  }
}
